* {
  margin: 0;
  padding: 0;
}

body {
  background-color: black;
  color: white;
}
#mu{
  visibility: hidden;
}
#muroc {
  height: 20px;
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(100%);
}

#muroclose{
  height: 20px;
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(100%) contrast(100%);
}

/*nav bar formatting*/
/*logo formatting start here*/
#logo {
  padding: 10px;
}

#logoh {
  padding: 10px;
  margin: 10px;
  color: white;
  font-size: 24px;
  font-weight: 800;
}

/*logo formatting ends here*/
#navbar {
  position: sticky;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  background-color: black;
  border-radius: 10px;
}

#navele {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.navli {
  margin: 2px;
  padding: 10px;
  list-style: none;
}

.nava {
  text-decoration: none;
  border-radius: 10px;
  padding: 10px;
  color: white;
}

.nava:hover {
  border: 2px solid white;
  color: white;
}

#hireme {
  text-decoration: none;
  color: white;
  padding: 10px;
  border: 2px solid white;
  border-radius: 10px;
  font-size: 20px;
  line-height: 6;
}

#hireme:hover {
  background-color: white;
  color: black;
  border: none;
}

/*nav bar ended*/
/*header section*/
#header {
  background-color: black;
